import { Layout as AntLayout } from 'antd';
import React, { Component, createRef, Fragment, RefObject } from 'react';
import { Post } from '../../contracts/post';
import Footer from '../Footer';
import Header from '../Header';
import ReadingProgressBar from '../ReadingProgressBar';
import RelatedPosts from '../RelatedPosts';
import './Layout.scss';

export interface Props {
	children: React.ReactNode;
	location: Location;
	posts?: Post[];
}

export class Layout extends Component<Props> {

	target: RefObject<HTMLDivElement> = createRef();

	render() {
		return (
			<Fragment>
				<ReadingProgressBar target={this.target} />
				<AntLayout className="layout-container">
					<Header location={this.props.location} />
					<div className="layout" ref={this.target}>
						<main>{this.props.children}</main>
					</div>
					{this.props.posts && <RelatedPosts posts={this.props.posts} />}
					<Footer />
				</AntLayout>
			</Fragment>
		);
	}
}

export default Layout;
