import { Col, Row } from 'antd';
import { graphql, Link, useStaticQuery } from 'gatsby';
import BackgroundImage from 'gatsby-background-image';
import React from 'react';
import './TopSection.scss';
export interface Props {
	title?: string;
	author?: string;
	slug?: string;
	date?: string;
	backgroundImage?: any;
}

export const TopSection = (props: Props) => {

	const { site, desktop } = useStaticQuery(graphql`
      query {
        site {
          siteMetadata {
            description
          }
		}
		desktop: file(relativePath: { eq: "top_section.png" }) {
			childImageSharp {
			  fluid(quality: 85, maxWidth: 1280) {
				...GatsbyImageSharpFluid_withWebp
			  }
			}
		  }
      }
	`);

	const imageData = props.backgroundImage ?? desktop.childImageSharp.fluid;

	return (
		<div className="boxed-layout-header header-small page-header">
			<div className="top-section-container">
				<Row justify="space-around" align="middle">
					<Col>
						<h1 className="hestia-title">{props.title || site.siteMetadata.description}</h1>
						{props.author && props.date && (
							<h4 className="author">
								Published by{` `}
								<Link to={`/${props.slug}`} title={props.author}><strong>{props.author}</strong></Link>{` on ${props.date}`}
							</h4>
						)}
					</Col>
				</Row>
			</div>
			<BackgroundImage
				className="header-filter"
				fluid={imageData}
				background={'#636363'}
			></BackgroundImage>
			<div className="header-filter"></div>
		</div>
	);
};

export default TopSection;
