import React from 'react';

import { Link } from 'gatsby';

import { Layout as AntLayout, Menu, Row, Col } from 'antd';

import './Header.scss';

import navbar_logo from '../../images/navbar_logo.png';

export interface Props {
	location: Location;
}

export const Header = (props: Props) => {
	const isHomePage = (props.location.pathname === '' || props.location.pathname === '/');
	const isAboutPage = (props.location.pathname === '/contact-us' || props.location.pathname === '/contact-us');

	return (
		<div className="header-container">
			<AntLayout.Header className="header">
				<Row>
					<Col span={12}>
						<Link to="/" title="DevScope Ninjas">
							<img width="76px" height="50px" className="logo" alt="DevScope Ninjas Logo" src={navbar_logo} />
						</Link>
					</Col>
					<Col span={12}>
						<Menu
							theme="light"
							mode="horizontal"
							defaultSelectedKeys={isHomePage ? ['blog'] : (isAboutPage ? ['who-we-are'] : ['blog'])}
						>

							<Menu.Item key="blog"><Link to="/" title="Blog">Blog</Link></Menu.Item>
							<Menu.Item key="consulting"><Link to="/consulting" title="What we do">What we do</Link></Menu.Item>
							<Menu.Item key="who-we-are"><Link to="/who-we-are" title="Who we are">Who we are</Link></Menu.Item>
							<Menu.Item key="contact-us"><Link to="/contact-us" title="Contact us">Contact us</Link></Menu.Item>
						</Menu>
					</Col>
				</Row>
			</AntLayout.Header>
		</div>
	);
};

export default Header;
