import Card from 'antd/lib/card';
import Col from 'antd/lib/grid/col';
import Row from 'antd/lib/grid/row';
import { Link } from 'gatsby';
import Image, { FluidObject } from 'gatsby-image';
import parse from 'html-react-parser';
import React from 'react';
import { CategoryTagInfo, Post } from '../../contracts/post';
import { decodeHtmlCharCodes } from '../../utils';
import './RelatedPosts.scss';

export interface Props {
	posts: Post[];
}

export const RelatedPosts = (props: Props) => {

	return (
		<>
			<Row justify="center">
				<h1 className="relatedPosts-title">Related Posts</h1>
			</Row>
			<Row className="posts" gutter={20} justify="center">
				{props.posts.map((node) => {
					const categories: CategoryTagInfo[] = (node.categories && node.categories.nodes! && node.categories.nodes!.length) > 0 ? node.categories.nodes!.filter((category) => category.name !== 'Uncategorized') : new Array<CategoryTagInfo>();
					const fluid: FluidObject | null = (node.featuredImage && node.featuredImage.node && node.featuredImage.node.localFile && node.featuredImage.node.localFile.childImageSharp && node.featuredImage.node.localFile.childImageSharp.fluid) ? node.featuredImage.node.localFile.childImageSharp.fluid : null;
					return (
						<Col xs={24} sm={12} md={12} lg={8} xl={8} xxl={8} id="primary"className="rowRecentPosts" key={node.slug}>
							<Card className="recentPosts">
								{(fluid && fluid.src && fluid.src.length > 0) && (
									<Link to={`/${node.slug}`} title={node.slug}>
										<Image className="card-image shadow-image" fluid={fluid} alt={node.title} title={node.title} />
									</Link>
								)}
								<div className="relatedPost-category">
									{categories && categories.length > 0 && categories.map((category, categoryIndex) => {
										return (
											<Link to={`/category/${category.slug}`} title={category.slug} key={category.slug}>
												<span className="name">{decodeHtmlCharCodes(category.name)}{categoryIndex < categories.length - 1 ? ', ' : null}</span>
											</Link>
										);
									})}
								</div>
								<Link to={`/${node.slug}`} title={node.slug}>
									<h2 className="relatedPost-title black-color">{decodeHtmlCharCodes(node.title)}</h2>
								</Link>
								<div className="post-excerpt">{parse(node.excerpt)}</div>
							</Card>
						</Col>
					);
				})}
			</Row >
		</>
	);
};

export default RelatedPosts;
