import { Row } from 'antd';
import { Link } from 'gatsby';
import React from 'react';
import './Footer.scss';

export interface Props {}

export const Footer = () => {
	return (
		<div className="footer-container">
			<Row justify="space-between" align="middle">
			<nav className="footer">
				<ul>
					<li><Link to="/" title="Blog">Blog</Link></li>
					<li><Link to="/consulting" title="What we do">What we do</Link></li>
					<li><Link to="/who-we-are" title="Who we are">Who we are</Link></li>
					<li><Link to="/contact-us" title="Contact us">Contact us</Link></li>
				</ul>
			</nav>
			</Row>
		</div>
	);
};

export default Footer;
