/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import { graphql, useStaticQuery } from 'gatsby';
import React from 'react';
import Helmet from 'react-helmet';
import { removeHtmlTags } from '../../utils';

export interface Props {
	isHomePage?: boolean;
	description: string;
	lang: string;
	meta: any[];
	image?: string;
	title: string;
}

export const SEO = (props: Props) => {
	const { site } = useStaticQuery(graphql`
      query {
        site {
          siteMetadata {
            title
            description
            author
          }
        }
      }
    `);

	const metaDescription = removeHtmlTags(props.description || site.siteMetadata.description);

	return (
		<Helmet
			htmlAttributes={{ lang: props.lang }}
			title={props.title}
			titleTemplate={`%s - ${props.isHomePage ? site.siteMetadata.description : site.siteMetadata.title}`}
			meta={[
				{
					name: 'description',
					content: metaDescription,
				},
				{
					property: 'image',
					content: props.image,
				},
				{
					property: 'og:image',
					content: props.image,
				},
				{
					property: 'title',
					content: props.title,
				},
				{
					property: 'og:title',
					content: props.title,
				},
				{
					property: 'description',
					content: metaDescription,
				},
				{
					property: 'og:description',
					content: metaDescription,
				},
				{
					property: 'og:type',
					content: 'website',
				},
				{
					name: 'twitter:card',
					content: 'summary',
				},
				{
					name: 'twitter:creator',
					content: site.siteMetadata.author,
				},
				{
					name: 'twitter:title',
					content: props.title,
				},
				{
					name: 'twitter:description',
					content: metaDescription,
				},
			].concat(props.meta)}
		/>
	);
};

SEO.defaultProps = {
	lang: 'en',
	meta: [],
	description: '',
};

export default SEO;
